
'use client';

import { useState, useEffect } from 'react';
import Link from 'next/link';
import type { User } from '@/types';
import { Button } from '@/components/ui/button';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu';
import LogoutButton from '@/components/auth/LogoutButton';
import { LogIn, UserCircle, LayoutDashboard, Loader2 } from 'lucide-react';
import { fetchUserSessionForClient } from '@/app/actions'; // Import the new Server Action

export default function UserNavOrLogin() {
  const [currentUser, setCurrentUser] = useState<User | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    async function fetchSession() {
      try {
        setLoading(true);
        const user = await fetchUserSessionForClient();
        setCurrentUser(user);
      } catch (error) {
        console.error("Failed to fetch user session:", error);
        setCurrentUser(null);
      } finally {
        setLoading(false);
      }
    }
    fetchSession();
  }, []);

  if (loading) {
    return (
      <Button variant="ghost" className="relative h-10 w-10 rounded-full">
        <Loader2 className="h-5 w-5 animate-spin" />
      </Button>
    );
  }

  if (currentUser) {
    return (
      <DropdownMenu>
        <DropdownMenuTrigger asChild>
          <Button variant="ghost" className="relative h-10 w-10 rounded-full">
            <Avatar className="h-10 w-10 border-2 border-primary">
              <AvatarImage src={currentUser.faceimg} alt={currentUser.nickname} />
              <AvatarFallback>
                {currentUser.nickname ? currentUser.nickname.charAt(0).toUpperCase() : <UserCircle />}
              </AvatarFallback>
            </Avatar>
          </Button>
        </DropdownMenuTrigger>
        <DropdownMenuContent className="w-56" align="end" forceMount>
          <DropdownMenuLabel className="font-normal">
            <div className="flex flex-col space-y-1">
              <p className="text-sm font-medium leading-none">{currentUser.nickname}</p>
              <p className="text-xs leading-none text-muted-foreground">
                {currentUser.type === 'staff' ? 'STAFF 账户' : `${currentUser.type.toUpperCase()} 用户`}
              </p>
            </div>
          </DropdownMenuLabel>
          <DropdownMenuSeparator />
          <DropdownMenuItem asChild className="cursor-pointer">
            <Link href="/dashboard">
              <LayoutDashboard className="mr-2 h-4 w-4" />
              <span>仪表盘</span>
            </Link>
          </DropdownMenuItem>
          <DropdownMenuSeparator />
          <LogoutButton />
        </DropdownMenuContent>
      </DropdownMenu>
    );
  }

  return (
    <Button asChild variant="default" className="bg-accent hover:bg-accent/90 text-accent-foreground">
      <Link href="/login">
        <LogIn className="mr-2 h-5 w-5" />
        登录
      </Link>
    </Button>
  );
}

    