
'use client'; 

import type { Metadata } from 'next';
import { usePathname } from 'next/navigation';
import { Geist, Geist_Mono } from 'next/font/google';
import './globals.css';
import { Toaster } from '@/components/ui/toaster';
import Header from '@/components/layout/Header';
import Footer from '@/components/layout/Footer';

const geistSans = Geist({
  variable: '--font-geist-sans',
  subsets: ['latin'],
});

const geistMono = Geist_Mono({
  variable: '--font-geist-mono',
  subsets: ['latin'],
});

export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  const pathname = usePathname();
  const showHeaderFooter = !pathname.startsWith('/admin') && !pathname.startsWith('/dashboard');

  return (
    <html lang="zh-CN">
      <head>
        <title>像素建筑馆</title>
        <meta name="description" content="展示精选的Minecraft建筑作品，并为它们投票。" />
        <link rel="icon" href="/favicon.ico" sizes="any" />
      </head>
      <body className={`${geistSans.variable} ${geistMono.variable} antialiased flex flex-col min-h-screen bg-background`}>
        {showHeaderFooter && <Header />}
        <main className={`flex-grow container mx-auto px-4 ${showHeaderFooter ? 'py-8' : 'py-0'}`}>
          {children}
        </main>
        {showHeaderFooter && <Footer />}
        <Toaster />
      </body>
    </html>
  );
}
